body {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loading-wrapper {
  text-align: center;
}

.loading-image {
  width: 80px;
}
.title {
  text-align: center;
  font-size: 4em;
  color: #c75c8c;
  font-weight: 200;
}
.panel {
  max-width: 700px;
  position: relative;
  margin: 20px auto;
  padding: 30px;
  text-align: center;
  background: #ffffff;
  border-radius: 3px;
  box-shadow: 2px 2px 10px #0000002e;
}

.close-button {
  position: absolute;
  right: 20px;
  top: 8px;
  background: none;
  border: none;
  font-size: 1.7em;
  font-weight: 100;
  padding: 10px;
  color: #8a8a8a;
  cursor: pointer;
}

.address-infos {
  margin-bottom: 40px;
  margin-top: -20px;
  text-align: left;
  color: #333;
}

.input-search {
  width: 140px;
  padding: 13px 19px;
  font-size: 1.3em;
  color: #7b7b7b;
  font-weight: 300;
  border: none;
  border-radius: 4px;
  background: #eaeaea;
  margin-right: 18px;
}

.input-search::-webkit-input-placeholder {
  color: #b1b1b1;
}

.input-search::-ms-input-placeholder {
  color: #b1b1b1;
}

.input-search::placeholder {
  color: #b1b1b1;
}

.primary-button {
  border: 1px solid #c75c8c;
  background: none;
  border-radius: 60px;
  padding: 10px 40px;
  font-size: 1em;
  font-weight: 500;
  color: #c75c8c;
  outline: none;
  cursor: pointer;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

.primary-button:hover {
  border: 1px solid #c75c8c;
  background: #c75c8c;
  color: #fff;
}
